.header {
    display: flex;
    height: 30px;
    /* 15 top botton, 20 left right */
    padding: 10px 20px;
    padding-top: 15px;
    justify-content: space-between;
    position: sticky;
    background-color: white;
    z-index: 100;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}

.header__left {
    display: flex;
    justify-content: space-evenly;
}

.header__left>img {
    height: 35px;
}

.header__input {
    display: flex;
    align-items: center;
    background-color: #eff2f5;
    padding: 10px;
    margin-left: 10px;
    border-radius: 33px;
}

.header__input>input {
    border: none;
    background-color: transparent;
    outline-width: 0;
}

.header__center {
    display: flex;
    flex: 1;
    justify-content: center;

}

.header__option {
    display: flex;
    align-items: center;
    padding: 0 30px;
    cursor: pointer;
}

.header__option:hover {
    background-color: #eff2f5;
    align-items: center;
    padding: 0 30px;
    border-bottom: 1px solid #2e81f4;
}

.header__option--active > .MuiSvgIcon-root {
    color: #2e81f4;
}

.header__option--active {
    border-bottom: 1px solid #2e81f4;
}

.header__option > .MuiSvgIcon-root {
    color: gray;
}

.header__option:hover > .MuiSvgIcon-root {
    color: #2e81f4
}

.header__info {
    display: flex;
    align-items: center;
    padding-right: 20px;
}

.header__info > div {
  width: 25px;
  height: 25px;
}

.header__info > h4 {
    margin-left: 10px;
}

.header__right {
    display: flex;
}

.header__right__options {
  margin-top: 0px;
  position: relative;
  top: -9px
}

.header__right__options > button {
  width: 10px;
  height: 10px;
  padding: 25px 30px;
}

#header-modal {
  margin-top: 35px;
}